import { Button, Card, Descriptions, Divider, Progress, Space, Tag, Select} from "antd";
import { PoweroffOutlined, RedoOutlined} from '@ant-design/icons';
import dayjs from 'dayjs';
import http from "../utils/http";
import { useEffect, useState } from "react";

function PvePanel() {

    //
    const [selectOptionDefault, setSelectOptionDefault] = useState(localStorage.getItem('selectOptionDefault'));
    const selectOptions = [
        {
            label: '',
            value: 3,
            data: {
                host: '',
                node: '',
                token: ''
            }
        },
        {
            label: '',
            value: 2,
            data: {
                host: '192.168.0.179:8006',
                node: 'pve',
                token: 'PVEAPIToken=root@pam!webApp=69eefd75-ac8f-45d8-b36d-8e91ffca33bf'
            }
        },
        {
            label: '',
            value: 1,
            data: {
                host: '',
                node: '',
                token: ''
            }
        },
    ]
    // data
    const [rep, setRep] = useState();
    let interval = null;


    // start 
    const vmAction = (node, vmid, action) => {

        // /api2/json/nodes/{node}/qemu/{vmid}/status/reboot
        switch (action) {
            case "reboot":
            case "shutdown":
            case "start":
                http.post(`http://localhost/pve/api2/json/nodes/${node}/qemu/${vmid}/status/${action}`, null,{headers:{Authorization: 'PVEAPIToken=root@pam!webApp=69eefd75-ac8f-45d8-b36d-8e91ffca33bf'}})
                .then(console.info).catch(console.error)
            break;
        
            default:
                break;
        }
    }
    const getData = (optionEntity) => {

        clearInterval(interval)

        interval = setInterval(()=>
        http.get(`http://localhost/pve/api2/json/nodes/pve/qemu`, {headers:{Authorization: 'PVEAPIToken=root@pam!webApp=69eefd75-ac8f-45d8-b36d-8e91ffca33bf'}})
        .then(res=>{
            
            setRep(res.data)
        }).catch(erorr => console.log),
        3000)
    }

    useEffect(
        ()=>{

            http.get(`http://localhost/pve/api2/json/nodes/pve/qemu`, {headers:{Authorization: 'PVEAPIToken=root@pam!webApp=69eefd75-ac8f-45d8-b36d-8e91ffca33bf'}})
        .then(res=>{
            
            setRep(res.data)
        }).catch(erorr => console.log)
        
            interval = setInterval(getData, 3000);

            return ()=> clearInterval(interval)
        },[]
    )


    return (

        <>

        
        <Space direction='vertical'>
        
        <Divider></Divider>
       
       <Space style={{display:'flex', flexDirection: 'row-reverse'}}>
            <Select defaultValue={2} options={selectOptions} onChange={(key, entity)=> {setSelectOptionDefault(key);localStorage.setItem('selectOptionDefault', key); getData(entity.data);} } />
       </Space>


        {
            rep?.data.sort((a,b)=>a.vmid - b.vmid).map(
                item => (


                    <Card 
                    
                        key={item.vmid}
                        title={item.name}

                        style={{maxWidth:"90vw"}}
                        extra={
                            <>
                            <Space>

                            {item.status === 'running' && <Button onClick={()=>vmAction("pve", item.vmid, "shutdown")} type="primary" danger icon={<PoweroffOutlined />}></Button>}
                            {item.status === 'running' && <Button onClick={()=>vmAction("pve", item.vmid, "reboot")} icon={<RedoOutlined />}></Button>}
                            {item.status !== 'running' && <Button onClick={()=>vmAction("pve", item.vmid, "start")} type="primary" icon={<PoweroffOutlined />} ></Button>}
                            </Space>
                            
                            </>
                        }
                    >

                        <Descriptions column={1}>
                            <Descriptions.Item label=''>{dayjs((item.uptime-(8*60*60))*1000).format('HH  mm  ss ')}</Descriptions.Item>
                            <Descriptions.Item label={item.cpus + 'C CPU'}>
                            <Progress percent={Math.round(item.cpu * 10000) / 100}  size="small" strokeColor={{from: '#108ee9',to: '#87d068'}}/>
                            </Descriptions.Item>
                            {/* <Descriptions.Item label='status'>{item.status}</Descriptions.Item> */}
                            <Descriptions.Item label={item.maxmem / 1073741824 + 'G MEM'}>
                                <Progress percent={Math.round( item.mem/item.maxmem*100*100)/100}  size="small" strokeColor={{from: '#108ee9',to: '#87d068'}}/>
                            </Descriptions.Item>
                        </Descriptions>
                    </Card>
                  
                )
            )
        }
        </Space>
        </>
    );

}

export default PvePanel;